  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const recycleStuff = ['bottle', 'book'];
  const garbageStuff = ['cup', 'fork', 'spoon', 'chair'];
  const compStuff = ['banana', 'apple', 'orange', 'sandwich', 'carrot'];

  const scannedItems = new Set([])

  const classes = useStyles();
  const [webcam, setWebCam] = useState(false);

  const enableWebcam = () => {
    setWebCam(true)
    runCoco()
    handleOpenBackDrop()
  }
  const disablWebcam = () => {
    setWebCam(false)
  }
  const webcamRef = useRef(null);
  const canvasRef = useRef(null);


  // Main function
  const runCoco = async () => {
    const net = await cocossd.load();
    console.log("Model Loaded");
    handleCloseBackDrop();
    setInterval(() => {
      detect(net);
    }, 200);
  };
  //console.log("BEFORE" + scannedItems)
  const detect = async (net) => {
    // Check data is available
    if (
      typeof webcamRef.current !== "undefined" &&
      webcamRef.current !== null &&
      webcamRef.current.video.readyState === 4
    ) {
      // Get Video Properties
      const video = webcamRef.current.video;
      const videoWidth = webcamRef.current.video.videoWidth;
      const videoHeight = webcamRef.current.video.videoHeight;

      // Set video width
      webcamRef.current.video.width = videoWidth;
      webcamRef.current.video.height = videoHeight;

      // Make Detections
      const obj = await net.detect(video);

      var textItem = '';
      //drawRect(obj, ctx);
      obj.forEach(prediction => {
        textItem = prediction['class'];
        if (recycleStuff.includes(textItem) && !scannedItems.has(textItem)) {
          setRecycleItems((prev) => {
            return [...prev, textItem]
          });
          setItem(""); 
          //sendRecycleData(); // UN-COMMENT THIS OUT IF USING BACK END
          setOpen(true); // COMMENT THIS OUT IF USING BACK END
          scannedItems.add(textItem)
        }
        if (garbageStuff.includes(textItem) && !scannedItems.has(textItem)) {
          setGarbageItem((prev) => {
            return [...prev, textItem]
          });
          setItem("");
          //sendGarbageData();// UN-COMMENT THIS OUT IF USING BACK END
          setOpen(true); // COMMENT THIS OUT IF USING BACK END
          scannedItems.add(textItem)
        }
        if (compStuff.includes(textItem) && !scannedItems.has(textItem)) {
          setCompItem((prev) => {
            return [...prev, textItem]
          });
          setItem("");
          setOpen(true); // COMMENT THIS OUT IF USING BACK END
          //sendCompostData(); // UN-COMMENT THIS OUT IF USING BACK END
          scannedItems.add(textItem)
        }
      });
      console.log(scannedItems)
    }
  };


